___CSS_LOADER_EXPORT___.push([module.id, `/* From Uiverse.io by vamsidevendrakumar */
.card {
    width: 300px;
    height: 150px;
    perspective: 1000px;
}

.card-inner {
    width: 100%;
    height: 100%;
    position: relative;
    transform-style: preserve-3d;
    transition: transform 0.999s;
}

.card:hover .card-inner {
    transform: rotateY(180deg);
}

.card-front,
.card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
}

.card-front {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
    background-color: #6A2C70;
    color: #fff;
    display: flex;
    align-items: center;
    border: 10px solid #6A2C70;
    border-radius: 10px;
    justify-content: center;
    font-size: 24px;
    transform: rotateY(0deg);
}

.card-back {
    background-color: #6f6f8b;
    color: #303030;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 10px solid #6f6f8b;
    border-radius: 10px;
    justify-content: center;
    font-size: 12px;
    gap: 20px;
    transform: rotateY(180deg);
}

.low {
    font-size: 12px;
    font-weight: 500;
    color: #82e082;
}

.medium {
    font-size: 12px;
    font-weight: 500;
    color: #f9c74f;
}

.high {
    font-size: 12px;
    font-weight: 500;
    color: #f3722c;
}

#description {
    font-size: 12px;
    font-weight: 500;
    color: #d4d4d4;
}

#completed {
    font-size: 12px;
    width: 20px;
    height: 20px;
    color: #d4d4d4;
    border: none;
    border-radius: 20px;
    background-color: #91002e;
    transition-duration: .2s;
}

#completed:hover {
    background-color: #740025;
}

#completed:active {
    transform: scale(0.9);
}`, "",{"version":3,"sources":["webpack://./src/styles/display.css"],"names":[],"mappings":"AAAA,0CAA0C;AAC1C;IACI,YAAY;IACZ,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,4BAA4B;IAC5B,4BAA4B;AAChC;;AAEA;IACI,0BAA0B;AAC9B;;AAEA;;IAEI,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,SAAS;IACT,yBAAyB;IACzB,WAAW;IACX,aAAa;IACb,mBAAmB;IACnB,0BAA0B;IAC1B,mBAAmB;IACnB,uBAAuB;IACvB,eAAe;IACf,wBAAwB;AAC5B;;AAEA;IACI,yBAAyB;IACzB,cAAc;IACd,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,0BAA0B;IAC1B,mBAAmB;IACnB,uBAAuB;IACvB,eAAe;IACf,SAAS;IACT,0BAA0B;AAC9B;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,eAAe;IACf,WAAW;IACX,YAAY;IACZ,cAAc;IACd,YAAY;IACZ,mBAAmB;IACnB,yBAAyB;IACzB,wBAAwB;AAC5B;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,qBAAqB;AACzB","sourcesContent":["/* From Uiverse.io by vamsidevendrakumar */\n.card {\n    width: 300px;\n    height: 150px;\n    perspective: 1000px;\n}\n\n.card-inner {\n    width: 100%;\n    height: 100%;\n    position: relative;\n    transform-style: preserve-3d;\n    transition: transform 0.999s;\n}\n\n.card:hover .card-inner {\n    transform: rotateY(180deg);\n}\n\n.card-front,\n.card-back {\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    backface-visibility: hidden;\n}\n\n.card-front {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    gap: 10px;\n    background-color: #6A2C70;\n    color: #fff;\n    display: flex;\n    align-items: center;\n    border: 10px solid #6A2C70;\n    border-radius: 10px;\n    justify-content: center;\n    font-size: 24px;\n    transform: rotateY(0deg);\n}\n\n.card-back {\n    background-color: #6f6f8b;\n    color: #303030;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    border: 10px solid #6f6f8b;\n    bord