___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Inter, 'Inter', 'Courier New', Courier, monospace;
}

body {
    background-color: #F0EAEF;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px;
}

header {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100px;
    background-color: #F0EAEF;
    gap: 20px;
}

/* From Uiverse.io by Yaya12085 */
.radio-inputs {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    border-radius: 0.5rem;
    background-color: #EEE;
    box-sizing: border-box;
    box-shadow: 0 0 0px 1px rgba(0, 0, 0, 0.06);
    padding: 0.25rem;
    min-width: 300px;
    font-size: 14px;
    gap: 1rem;
}

.radio-inputs .radio {
    flex: 1 1 auto;
    text-align: center;
}

.radio-inputs .radio input {
    display: none;
}

.radio-inputs .radio .name {
    display: flex;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    border-radius: 0.5rem;
    border: none;
    padding: .5rem 0;
    color: rgba(51, 65, 85, 1);
    transition: all .15s ease-in-out;
}

.radio-inputs .radio input:checked+.name {
    background-color: #fff;
    font-weight: 600;
}

/* From Uiverse.io by Yaya12085 */
.Btn {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 40px;
    height: 40px;
    border-radius: calc(40px/2);
    border: none;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition-duration: .3s;
    box-shadow: 0 0 0px 1px rgba(0, 0, 0, 0.06);
    background-color: #EEE;
}

/* plus sign */
.sign {
    width: 100%;
    font-size: 2.2em;
    color: rgba(51, 65, 85, 1);
    transition-duration: .3s;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 5px;
}

.task>.sign {
    font-size: 1.5em;
    padding-bottom: 3px;
}

/* text */
.text {
    position: absolute;
    right: 0%;
    width: 0%;
    opacity: 0;
    color: rgba(51, 65, 85, 1);
    font-size: 16px;
    font-weight: 600;
    transition-duration: .3s;
}

.task>.text {
    font-size: 14px;
    font-weight: 500;
}

/* hover effect on button width */
.Btn:hover {
    width: 125px;
    transition-duration: .3s;
}

.task.Btn:hover {
    width: 100px;
}

.Btn:hover .sign {
    width: 30%;
    transition-duration: .3s;
    padding-left: 10px;
    padding-bottom: 5px;
}

.task.Btn:hover .sign {
    padding-bottom: 3px;
}

/* hover effect button's text */
.Btn:hover .text {
    opacity: 1;
    width: 70%;
    transition-duration: .3s;
    padding-right: 5px;
}

/* button click effect*/
.Btn:active {
    transform: scale(0.95);
}

main {
    width: 1000px;
    display: grid;
    grid-template-columns: repeat(auto-fit, 300px);
    gap: 10px;
    padding: 1rem;
    justify-content: center;
}

@media (max-width: 960px) {
    main {
        width: 100%;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/index.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,8DAA8D;AAClE;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,WAAW;IACX,aAAa;IACb,yBAAyB;IACzB,SAAS;AACb;;AAEA,iCAAiC;AACjC;IACI,kBAAkB;IAClB,aAAa;IACb,eAAe;IACf,qBAAqB;IACrB,sBAAsB;IACtB,sBAAsB;IACtB,2CAA2C;IAC3C,gBAAgB;IAChB,gBAAgB;IAChB,eAAe;IACf,SAAS;AACb;;AAEA;IACI,cAAc;IACd,kBAAkB;AACtB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,eAAe;IACf,mBAAmB;IACnB,uBAAuB;IACvB,qBAAqB;IACrB,YAAY;IACZ,gBAAgB;IAChB,0BAA0B;IAC1B,gCAAgC;AACpC;;AAEA;IACI,sBAAsB;IACtB,gBAAgB;AACpB;;AAEA,iCAAiC;AACjC;IACI,aAAa;IACb,mBAAmB;IACnB,2BAA2B;IAC3B,WAAW;IACX,YAAY;IACZ,2BAA2B;IAC3B,YAAY;IACZ,eAAe;IACf,kBAAkB;IAClB,gBAAgB;IAChB,wBAAwB;IACxB,2CAA2C;IAC3C,sBAAsB;AAC1B;;AAEA,cAAc;AACd;IACI,WAAW;IACX,gBAAgB;IAChB,0BAA0B;IAC1B,wBAAwB;IACxB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA,SAAS;AACT;IACI,kBAAkB;IAClB,SAAS;IACT,SAAS;IACT,UAAU;IACV,0BAA0B;IAC1B,eAAe;IACf,gBAAgB;IAChB,wBAAwB;AAC5B;;AAEA;IACI,eAAe;IACf,gBAAgB;AACpB;;AAEA,iCAAiC;AACjC;IACI,YAAY;IACZ,wBAAwB;AAC5B;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,UAAU;IACV,wBAAwB;IACxB,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;AACvB;;AAEA,+BAA+B;AAC/B;IACI,UAAU;IACV,UAAU;IACV,wBAAwB;IACxB,kBAAkB;AACtB;;AAEA,uBAAuB;AACvB;IACI,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,aAAa;IACb,8CAA8C;IAC9C,SAAS;IACT,aAAa;IACb,uBAAuB;AAC3B;;AAEA;IACI;QACI,WAAW;IACf;AACJ","sourcesContent":["* {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n    font-family: Inter, 'Inter', 'Courier New', Courier, monospace;\r\n}\r\n\r\nbody {\r\n    background-color: #F0EAEF;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    padding: 40px;\r\n}\r\n\r\nheader {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    width: 100%;\r\n    height: 100px;\r\n    backgr